/****** Header ******/
.header-container {
  height: 40px;
  background: #474a51;
  color: azure;
  font-size: 20px;
  padding: 30px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  transition: color linear 200ms;
  -webkit-transition: color linear 200ms;
  -moz-transition: color linear 200ms;
  -ms-transition: color linear 200ms;
  -o-transition: color linear 200ms;
}
.header-logo {
  position: absolute;
  top: calc(50% -15px);
  left: 20px;
}
.header-container a.active {
  color: rgb(9, 194, 194);
  transition: color linear 200ms;
  -webkit-transition: color linear 200ms;
  -moz-transition: color linear 200ms;
  -ms-transition: color linear 200ms;
  -o-transition: color linear 200ms;
}
.header-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
/****** Main ******/
.main-container {
  min-height: 100vh;
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 20px 20px;
}
.page-title {
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 2px;
}
/****** Footer ******/
.footer-container {
  height: 60px;
  background: #474a51;
  color: azure;
  font-size: 20px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
