.cards-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
  padding: 100px 20px;
}
.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
  width: auto;
  flex: 0 1 350px;
  min-width: 310px;
  border: 1px solid #828282;
  border-radius: 5px;
  padding: 15px;
  height: 420px;
}
.card-container:hover {
  border: 2px solid #5c5b5b;
  transform: scale(1.02);
  -webkit-transform: scale(1.02);
  -moz-transform: scale(1.02);
  -ms-transform: scale(1.02);
  -o-transform: scale(1.02);
}
.card-img {
  height: 250px;
}
.card-title {
  text-align: center;
  height: 50px;
}
.item-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
}
.system-content {
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 10px);
  font-weight: 700;
  font-size: 22px;
}
